<template>
    <div>
        <div class="contentBox" v-for="(value,index) in failedList">
            <dl>
                <dt class="boxTitle">:
                    <span>{{ value.placename }}</span>
                </dt>
                <dd>
                    :
                    <span>{{ value.placeaddress }}</span>
                </dd>
                <dd>
                    :
                    <span>{{ value.admin }},{{ value.mobile }}</span>
                </dd>
                <dd>
                    :
                    <span v-for="(value2,index2) in failedList[index].learnlist">{{ value2.learnname }}&nbsp;&nbsp;</span>
                </dd>
                <dd>
                    :
                    <span>{{ value.stunum }}</span>
                </dd>
                <dd>
                    :
                    <span>{{ value.starttime }}~{{ value.endtime }}</span>
                </dd>
                <dd>
                    :
                    <span>{{ value.comment }}</span>
                </dd>
            </dl>
        </div>
    </div>
</template>
<script>
    import xieFn from '../../../vuex/xie'
    export default {
        data() {
            return {
                failedList:'',
            }
        },
        mounted() {
            this.failedList=xieFn.getMyAppointInfo(2)
        },
        methods:{

        }
    }
</script>




